import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DashboardService } from '@app/modulos/dashboard/servicios/dashboard.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { TiposDeDescargas } from '@app/modulos/dashboard/modelo/enums/TiposDeDescargas.enum';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-descargar-documento',
  templateUrl: './descargar-documento.component.html',
})
export class DescargarDocumentoComponent implements OnInit {
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private customModalService: CustomModalService,
    private dashboarService: DashboardService
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params) => {
      const idDocumento = params.id_documento;
      const tipoDescarga = this.route.snapshot.data.tipoDescarga;
      idDocumento
        ? this.descargarArchivo(idDocumento, tipoDescarga)
        : this.redirecionarTareas();
    });
  }

  descargarArchivo(idDocumento, tipoDescarga): void {
    const respuesta: Observable<any> =
      tipoDescarga === TiposDeDescargas.DOCUMENTAL
        ? this.dashboarService.descargarDocumento(idDocumento, 'documento')
        : this.dashboarService.descargarConstanciaFiscal(idDocumento);

    respuesta.subscribe(
      (x) => this.redirecionarTareas(),
      () =>
        this.customModalService
          .abrirModal(CustomModalType.ERROR)
          .afterClosed()
          .subscribe(() => {
            this.redirecionarTareas();
          })
    );
  }
  redirecionarTareas(): void {
    this.router.navigate(['dashboard']);
  }
}
