import { Component, OnInit, Input } from '@angular/core';
import { IAnexo } from '@app/modulos/dashboard/modelo/interfaces/IAnexo.type';
import { MatDialog } from '@angular/material/dialog';
import { DashboardService } from '@app/modulos/dashboard/servicios/dashboard.service';
import { VerArchivoComponent } from '@app/modulos/dashboard/componentes/ver-archivo/ver-archivo.component';

@Component({
  selector: 'app-anexos-detalle-documento',
  templateUrl: './anexos-detalle-documento.component.html',
  styleUrls: ['./anexos-detalle-documento.component.scss'],
})
export class AnexosDetalleDocumentoComponent implements OnInit {
  @Input() anexos: IAnexo[];
  @Input() nombreDocumento: string;

  constructor(
    private dialog: MatDialog,
    private dashboarService: DashboardService
  ) {}

  ngOnInit() {}

  descargarArchivo(idDocumento, nombreTipoDocumento) {
    this.dashboarService
      .descargarDocumento(idDocumento, nombreTipoDocumento)
      .subscribe((x) => {
        console.log('respueta cuando finaliza', x);
      });
  }

  mostrarModalVerDocumento(idDocumento) {
    this.dialog.open(VerArchivoComponent, {
      height: '70%',
      width: '50%',
      data: {
        idDocumento,
      },
    });
  }
}
