import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { TiposDatoEmpresaMexico } from '@app/modulos/dashboard/modelo/enums/TiposDatoEmpresaMexico.enum';

@Component({
  selector: 'app-modal-datos-empresa',
  templateUrl: './modal-datos-empresa.component.html',
  styleUrls: ['./modal-datos-empresa.component.scss'],
})
export class ModalDatosEmpresaComponent implements OnInit {
  tiposDatos = TiposDatoEmpresaMexico;
  tipo: TiposDatoEmpresaMexico;
  valor: any;

  constructor(
    public dialogRef: MatDialogRef<ModalDatosEmpresaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.tipo = data.tipo;
    this.valor = data.valor;
  }

  ngOnInit() {}

  close() {
    this.dialogRef.close();
  }

  guardar() {
    this.dialogRef.close({ tipo: this.tipo, valor: this.valor });
  }
}
