import { Observable } from 'rxjs';
import { EstadoTarea } from '../../modelo/enums/EstadosTarea.enum';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ModalComentariosComponent } from '../../componentes/modal-comentarios/modal-comentarios.component';
import { ActivatedRoute, Router } from '@angular/router';
import { DashboardService } from '../../servicios/dashboard.service';
import {
  AbstractControl,
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { IDocumento } from '../../modelo/interfaces/IDocumento.type';
import { EstadosDocumento } from '../../modelo/enums/EstadosDocumento.enum';
import { ModalDatosEmpresaComponent } from '@app/modulos/dashboard/componentes/modal-datos-empresa/modal-datos-empresa.component';
import { IDatosEmpresaMexicoResponse } from '@app/modulos/dashboard/modelo/interfaces/IDatosEmpresaMexicoResponse.type';
import { TiposDatoEmpresaMexico } from '@app/modulos/dashboard/modelo/enums/TiposDatoEmpresaMexico.enum';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { IAnexo } from '@app/modulos/dashboard/modelo/interfaces/IAnexo.type';
import { ITareaDocumental } from '@app/modulos/dashboard/modelo/interfaces/ITareaDocumental';
import { map } from 'rxjs/operators';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-detalle-tarea',
  templateUrl: './detalle-tarea.component.html',
  styleUrls: ['./detalle-tarea.component.scss'],
})
export class DetalleTareaComponent implements OnInit {
  ultimaVersion: number;
  idTarea: number;
  estadoDocumento: Observable<EstadoTarea>;
  estadoTarea = EstadoTarea;
  documentosForm: FormGroup;
  tiposRechazo;
  estadosDocumento = EstadosDocumento;
  tiposDato = TiposDatoEmpresaMexico;
  uuidEmpresa: string;
  esperaRespuesta = true;
  tareaDocumental: ITareaDocumental;

  public get documentosFormulario() {
    return this.documentosForm.get('documentos') as FormArray;
  }

  constructor(
    private _location: Location,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    private dashboarService: DashboardService,
    private customModalService: CustomModalService
  ) {}

  ngOnInit() {
    this.documentosForm = this.fb.group({
      documentos: this.fb.array([]),
      rfc: ['', Validators.pattern(/^[A-Za-z0-9]+$/)],
      razonSocial: ['', Validators.pattern(/^[aA-zZ0-9&.,' -]+$/)],
    });
    this.dashboarService
      .consultarListaTiposRechazo()
      .subscribe((x) => (this.tiposRechazo = x));
    this.route.params.subscribe((params) => {
      this.idTarea = +params['id_tarea'];
      const control = this.documentosForm.controls['documentos'] as FormArray;
      this.estadoDocumento = this.dashboarService
        .consultarEstadoTareaDocumental(this.idTarea)
        .pipe(map((x: { estado: string }) => EstadoTarea[x.estado]));
      this.dashboarService
        .consultarTareasDocumentalesPorId(this.idTarea)
        .subscribe(
          (tareaDocumental: ITareaDocumental) => {
            this.tareaDocumental = tareaDocumental;
            const documentosBack = tareaDocumental.documentos;
            this.uuidEmpresa = this.obtenerUUIDEmpresa(documentosBack);
            if (documentosBack) {
              documentosBack.sort((a, b) => b.idTarea - a.idTarea);
              this.ultimaVersion = documentosBack[0].idTarea;
              documentosBack.forEach((documentoBack) => {
                control.push(
                  this.iniciarDocumento(documentoBack, this.idTarea)
                );
              });
            }
            if (this.uuidEmpresa) {
              this.dashboarService
                .consultarDatosEmpresaMexico(this.uuidEmpresa)
                .subscribe(
                  (values: IDatosEmpresaMexicoResponse) => {
                    this.documentosForm.patchValue({
                      rfc: values.rfc,
                      razonSocial: values.razonSocial,
                    });
                    this.documentosForm
                      .get('documentos')
                      .valueChanges.subscribe(
                        () => '',
                        (err) => console.error(err)
                      );
                  },
                  (err) => {
                    if (err.status !== 404) {
                      this.mostarModalError();
                      console.error('No se poseen datos existentes', err);
                    }
                  }
                );
            }
          },
          (error) => {
            console.error('No se encontraron Tareas', error);
            this.router
              .navigate(['/dashboard'])
              .then(() => this.mostarModalError())
              .catch((reason) => console.error(reason));
          }
        );
    });
  }

  validarEspaciosBlancos(control: AbstractControl) {
    const valor = control.value;
    if (valor && valor.trim() !== '') {
      control.setValue(valor.trim());
    }
  }

  iniciarDocumento(value, idTarea) {
    const anexos: IAnexo[] = value.anexos.sort((a, b) => a.anexo - b.anexo);
    return this.fb.group(
      {
        nombreDocumento: [value.nombreDocumento],
        fechaCarga: [value.fechaCarga],
        idTarea: [value.idTarea],
        anexos: new FormControl(anexos),
        estado: [
          value.estado,
          Validators.compose([Validators.required, this.validarEstado]),
        ],
        tipoRechazo: [value.tipoRechazo, Validators.required],
        comentario: [value.comentario],
        procesado: value.idTarea !== idTarea,
        version: value.version,
      },
      { validator: this.validarRechazo }
    );
  }

  esPosibleEnviarFormulario(a: boolean, b: boolean): boolean {
    return b ? !a && b : true;
  }

  documentosFiltrados(documentos) {
    let documentosFiltrados = [];

    documentos
      .filter((doc) => doc.idTarea === this.idTarea)
      .forEach((value: IDocumento) => {
        documentosFiltrados = documentosFiltrados.concat(
          value.anexos.map((x) => ({
            uuidDocumento: x.uuidDocumento,
            estado: value.estado,
            tipoRechazo: value.tipoRechazo ? value.tipoRechazo : 'DESCONOCIDO',
            comentario: value.comentario,
          }))
        );
      });

    return documentosFiltrados;
  }

  seleccionadoRFC(b, a) {
    const esRFC = new RegExp('RFC');
    if (!esRFC.exec(a)) {
      return;
    }
    b === 'APROBADO'
      ? this.documentosForm.get('rfc').setValidators([Validators.required])
      : this.documentosForm.get('rfc').setValidators(null);
    this.documentosForm.get('rfc').updateValueAndValidity();
  }

  trackByFn(index, item) {
    return index;
  }

  enviar(documentosForm: FormGroup) {
    const formularioDatos = documentosForm.value;
    if (documentosForm.valid && this.esperaRespuesta) {
      const docsFiltrados = this.documentosFiltrados(
        formularioDatos.documentos
      );
      const rfc: string = formularioDatos.rfc;
      const data = {
        uuidEmpresa: this.uuidEmpresa,
        razonSocial: formularioDatos.razonSocial,
        rfc,
      };
      this.esperaRespuesta = false;
      this.dashboarService
        .enviarComandoFinalizarTareaDocumental({
          idTarea: this.idTarea,
          documentos: docsFiltrados,
        })
        .then(() =>
          rfc !== ''
            ? this.dashboarService.registrarDatosEmpresaMexico(data)
            : 'no registrar Datos empresa'
        )
        .then(() => {
          this.esperaRespuesta = false;
          this.mostarModalSuccess();
          this._location.back();
        })
        .catch((err) => {
          this.mostarModalError();
          this.esperaRespuesta = true;
        });
    }
  }

  validarRechazo(control: AbstractControl): { [key: string]: any } {
    const estado = control.get('estado').value;
    const tipoRechazo = control.get('tipoRechazo').value;
    const comentario = control.get('comentario').value;
    if (estado === EstadosDocumento.RECHAZADO) {
      if (tipoRechazo === 'DESCONOCIDO') {
        return { tipoRechazo: { value: control.get('tipoRechazo').value } };
      } else if (!comentario) {
        return { comentario: { value: control.get('comentario').value } };
      }
    }
    return null;
  }

  mostrarModal(documentoCtrl: any, soloLectura?: boolean) {
    const estado = documentoCtrl.value['estado'];
    const modal = this.dialog.open(ModalComentariosComponent, {
      data: {
        comentario: documentoCtrl.value['comentario'],
        color: estado === EstadosDocumento.APROBADO ? 'azul' : 'naranja',
        soloLectura: soloLectura,
      },
      panelClass: 'medium-screen-dialog',
    });
    modal.afterClosed().subscribe((nuevoComentario) => {
      const mensaje =
        nuevoComentario !== ' '
          ? nuevoComentario || documentoCtrl.controls['comentario'].value
          : null;
      documentoCtrl.controls['comentario'].setValue(mensaje);
    });
    return modal;
  }

  validarEstado(control: AbstractControl): { [key: string]: any } {
    return EstadosDocumento.PENDIENTE === control.value
      ? { pendiente: { value: control.value } }
      : null;
  }

  abrirModal(tipo: TiposDatoEmpresaMexico) {
    const modal = this.dialog.open(ModalDatosEmpresaComponent, {
      data: {
        tipo: tipo,
        valor: this.getFormValue(tipo),
      },
      panelClass: 'medium-screen-dialog',
    });
    modal.afterClosed().subscribe((obj) => {
      if (obj) {
        this.setFormValue(obj.tipo, obj.valor);
      }
    });
    return modal;
  }

  getFormValue(tipo: TiposDatoEmpresaMexico) {
    return this.documentosForm.get([tipo]).value;
  }

  private setFormValue(tipo: TiposDatoEmpresaMexico, valor: any) {
    this.documentosForm.patchValue({ [tipo]: valor });
  }

  private obtenerUUIDEmpresa(documentosBack: IDocumento[]) {
    if (documentosBack && documentosBack.length !== 0) {
      return documentosBack[0].uuidEmpresa;
    }
    return null;
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }
}
