import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { DashboardService } from '@app/modulos/dashboard/servicios/dashboard.service';
import { ITareaGenerica } from '@app/modulos/dashboard/modelo/interfaces/ITareaGenerica.type';
import { ModalComentarioTareaGenericaComponent } from '@app/modulos/dashboard/componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { Location } from '@angular/common';
import { EstadoTarea } from '@app/modulos/dashboard/modelo/enums/EstadosTarea.enum';

@Component({
  selector: 'app-tarea-generica',
  templateUrl: './tarea-generica.component.html',
  styleUrls: ['./tarea-generica.component.scss'],
})
export class TareaGenericaComponent implements OnInit {
  detalleTarea: ITareaGenerica;
  bloquearBoton = false;
  EstadoTarea = EstadoTarea;
  esReintentable = false;

  constructor(
    private location: Location,
    private router: Router,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private dashboarService: DashboardService,
    private customModalService: CustomModalService
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params) => {
      const idTarea = +params.id_tarea;
      this.dashboarService.consultarTareaPorId(idTarea).subscribe(
        (tarea) => {
          this.detalleTarea = tarea;
          this.esReintentable =
            this.detalleTarea.colaOriginal && this.detalleTarea.eventoOriginal;
        },
        (error) => {
          this.router
            .navigate(['/dashboard'])
            .then(() => this.obtenerErrorConsulta(error.data.mensaje))
            .catch((reason) => console.error(reason));
          console.error('ERROR consulta tarea generica', error);
        }
      );
    });
  }

  abrirModalComentario(estado: EstadoTarea) {
    const modal = this.dialog.open(ModalComentarioTareaGenericaComponent, {
      data: { estado },
      panelClass: 'medium-screen-dialog',
    });
    modal.afterClosed().subscribe((comentario) => {
      if (comentario) {
        this.detalleTarea = Object.assign({}, this.detalleTarea, {
          comentario,
        });
        if (estado === EstadoTarea.REINTENTADA) {
          this.reintentarTareaGenerica(this.detalleTarea);
        } else {
          this.finalizarTareaGenerica(this.detalleTarea);
        }
      }
    });
    return modal;
  }

  finalizarTareaGenerica(detalleTarea: ITareaGenerica) {
    this.bloquearBoton = true;
    this.dashboarService
      .finalizarTareaGenerica({
        idTarea: detalleTarea.id,
        comentario: detalleTarea.comentario,
      })
      .then(() => {
        this.bloquearBoton = false;
        this.mostarModalSuccess();
        this.location.back();
      })
      .catch((err) => {
        this.bloquearBoton = false;
        this.mostarModalError();
      });
  }

  reintentarTareaGenerica(detalleTarea: ITareaGenerica) {
    this.bloquearBoton = true;
    this.dashboarService
      .reintentarTareaGenerica({
        idTarea: detalleTarea.id,
        comentario: detalleTarea.comentario,
      })
      .then(() => {
        this.bloquearBoton = false;
        this.mostarModalSuccess();
        this.location.back();
      })
      .catch((err) => {
        this.bloquearBoton = false;
        this.mostarModalError();
      });
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }

  private obtenerErrorConsulta(error: string) {
    switch (error) {
      case 'tarea.detalle.pais':
        return this.customModalService.abrirModal(CustomModalType.WARNING, {
          mensaje:
            'La tarea que intenta consultar no corresponde al pas en sesin.',
        });
      case 'tarea.detalle.noexiste':
        return this.customModalService.abrirModal(CustomModalType.ERROR);
      default:
        return this.customModalService.abrirModal(CustomModalType.ERROR);
    }
  }
}
