import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { TareasComponent } from './vistas/tareas/tareas.component';
import { DetalleTareaComponent } from './vistas/detalle-tarea/detalle-tarea.component';
import { MaterialModule } from '@material/material.module';
import { ModalComentariosComponent } from './componentes/modal-comentarios/modal-comentarios.component';
import { HeaderComponent } from './componentes/header/header.component';
import { DashboardService } from './servicios/dashboard.service';
import {
  FormGroupDirective,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import { HumanizePipe } from '@app/shared/pipes/HumanizePipe.pipe';
import { ModalDatosEmpresaComponent } from './componentes/modal-datos-empresa/modal-datos-empresa.component';
import { VerArchivoComponent } from './componentes/ver-archivo/ver-archivo.component';
import { AnexosDetalleDocumentoComponent } from './componentes/anexos-detalle-documento/anexos-detalle-documento.component';
import { TareaGenericaComponent } from './vistas/tarea-generica/tarea-generica.component';
import { ModalComentarioTareaGenericaComponent } from './componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { DescargarDocumentoComponent } from './componentes/descargar-documento/descargar-documento.component';
import { DashboardComponent } from '@app/modulos/dashboard/dashboard.component';
import { RouterModule, Routes } from '@angular/router';
import { TiposDeDescargas } from '@app/modulos/dashboard/modelo/enums/TiposDeDescargas.enum';
import { SharedModule } from '@app/shared/shared.module';
import { TareaRevisionListasNegrasComponent } from '@app/modulos/dashboard/vistas/tarea-revision-listas-negras/tarea-revision-listas-negras.component';
import { InvitacionesEmpresaComponent } from '@app/shared/componentes/invitaciones-empresa/invitaciones-empresa.component';
import { PaisAmbiente } from '@app/shared/modelos/enums/paisAmbiente.enum';
import { PaisAmbienteGuard } from '@core/guards/pais-ambiente.guard';
import { MatMenuModule } from '@angular/material/menu';
import { MenuIzquierdoComponent } from '@app/modulos/dashboard/componentes/menu-izquierdo/menu-izquierdo.component';
import { TareasPorTipoComponent } from '@app/modulos/dashboard/vistas/tareas-por-tipo/tareas-por-tipo.component';
import { TareaDocumentosComponent } from '@dashboard/vistas/tarea-documentos/tarea-documentos.component';
import { IncentivosComponent } from '@dashboard/vistas/incentivos/incentivos.component';
import { QiipLibModule } from '@qiip/lib';

const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children: [
      {
        path: '',
        component: TareasComponent,
        pathMatch: 'full',
      },
      {
        path: 'tareas-por-tipo',
        component: TareasPorTipoComponent,
        pathMatch: 'full',
      },
      {
        path: 'detalle-tarea/:id_tarea',
        component: DetalleTareaComponent,
        canLoad: [PaisAmbienteGuard],
        data: { paises: [PaisAmbiente.MX] },
      },
      {
        path: 'detalle-generico/:id_tarea',
        component: TareaGenericaComponent,
      },
      {
        path: 'descargar-documentos/:id_documento',
        component: DescargarDocumentoComponent,
        data: {
          tipoDescarga: TiposDeDescargas.DOCUMENTAL,
          paises: [PaisAmbiente.MX],
        },
        canLoad: [PaisAmbienteGuard],
      },
      {
        path: 'descargar-constancias-fiscales/:id_documento',
        component: DescargarDocumentoComponent,
        data: {
          tipoDescarga: TiposDeDescargas.CONSTANCIA_FISCAL,
          paises: [PaisAmbiente.MX],
        },
        canLoad: [PaisAmbienteGuard],
      },
      {
        path: 'revision-listas-negras/:id_tarea',
        component: TareaRevisionListasNegrasComponent,
        canLoad: [PaisAmbienteGuard],
        data: { paises: [PaisAmbiente.MX] },
      },
      {
        path: 'invitaciones-empresa',
        component: InvitacionesEmpresaComponent,
        canLoad: [PaisAmbienteGuard],
        data: { paises: [PaisAmbiente.MX] },
      },
      {
        path: 'revision-documentos/:idTarea',
        component: TareaDocumentosComponent,
      },
      {
        path: 'ahorrador',
        loadChildren: () =>
          import('./ahorrador/ahorrador.module').then((m) => m.AhorradorModule),
      },
      {
        path: 'incentivos',
        component: IncentivosComponent,
      },
      { path: '**', redirectTo: 'login' },
    ],
  },
];

@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    MatMenuModule,
    QiipLibModule,
    RouterModule.forChild(routes),
  ],
  declarations: [
    DashboardComponent,
    TareasComponent,
    DetalleTareaComponent,
    ModalComentariosComponent,
    HeaderComponent,
    ModalDatosEmpresaComponent,
    VerArchivoComponent,
    AnexosDetalleDocumentoComponent,
    TareaGenericaComponent,
    ModalComentarioTareaGenericaComponent,
    DescargarDocumentoComponent,
    TareaRevisionListasNegrasComponent,
    MenuIzquierdoComponent,
    TareasPorTipoComponent,
    TareaDocumentosComponent,
    IncentivosComponent,
  ],
  entryComponents: [
    ModalComentariosComponent,
    ModalDatosEmpresaComponent,
    ModalComentarioTareaGenericaComponent,
    VerArchivoComponent,
  ],
  providers: [FormGroupDirective, DatePipe, DashboardService, HumanizePipe],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class DashboardModule {}
