import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { DashboardService } from '@dashboard/servicios/dashboard.service';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalType, QiipModalService, QiipSpinnerService } from '@qiip/lib';
import {
  GenerarIncentivoDTO,
  GenerarIncentivosDTO,
  IncentivosGeneradosDTO,
  InvitacionDTO,
} from '@dashboard/modelo/interfaces/Incentivos';
import { EstadoInvitacion } from '@dashboard/modelo/enums/Incentivos.enum';
import { Store } from '@ngrx/store';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import * as dayjs from 'dayjs';

interface InvitacionData {
  // datos invitacion
  idInvitacion: number;
  nombreRemitente: string;
  idRemitente: number;
  idUsuarioRemitente: string;
  contratoRemitente: string;
  montoRemitente: number;
  idIncentivoRemitente: number;
  nombreInvitado: string;
  idInvitado: number;
  idUsuarioInvitado: string;
  contratoInvitado: string;
  montoInvitado: number;
  idIncentivoInvitado: number;
  celularInvitado: string;
  fechaInvitado: Date;
  estadoInvitacion: EstadoInvitacion;
  // select
  remitenteSeleccionado: boolean;
  remitenteHabilitado: boolean;
  invitadoSeleccionado: boolean;
  invitadoHabilitado: boolean;
}

@Component({
  selector: 'app-incentivos',
  templateUrl: './incentivos.component.html',
  styleUrls: ['./incentivos.component.scss'],
})
export class IncentivosComponent implements OnInit {
  pageSizeOptions = [10, 25, 50, 100];
  valorFiltro: string;

  columnasVisibles: string[] = [];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  estadosQueryParams: EstadoInvitacion[] = [];
  estadosSeleccionados: EstadoInvitacion[];
  listaEstados = [
    EstadoInvitacion.NUEVA,
    EstadoInvitacion.APLICA_INCENTIVO,
    EstadoInvitacion.NO_APLICA_INCENTIVO,
    EstadoInvitacion.CON_INCENTIVO,
    EstadoInvitacion.VENCIDA,
  ];
  invitaciones: InvitacionData[] = [];
  totalRemitente: number;
  totalInvitado: number;
  EstadoInvitacion = EstadoInvitacion;
  private correoOperador: string;
  private invitacionesTemp: InvitacionDTO[];
  seleccionarTodosRemitentes = false;
  seleccionarTodosInvitados = false;
  maxDate = new Date();
  form = new FormGroup({
    periodo: new FormControl(new Date(), [Validators.required]),
  });

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private dashboardService: DashboardService,
    private qiipModalService: QiipModalService,
    private qiipSpinnerService: QiipSpinnerService,
    private store: Store<any>
  ) {}

  ngOnInit() {
    this.initColumnas();
    this.consultarIncentivos();
    this.store
      .select('usuario', 'email')
      .subscribe((value) => (this.correoOperador = value));
  }

  aplicarFiltro(event) {
    const valorDelFiltro = (event.target as HTMLInputElement).value;
    this.filtrar(valorDelFiltro);
    this.reiniciarSeleccionTodos();
  }

  filtrar(texto) {
    if (texto === null || texto === '') {
      this.borrarFiltro();
    } else if (texto) {
      this.dataSource.filter = texto.trim().toLowerCase();

      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    }
  }

  borrarFiltro() {
    this.valorFiltro = '';
    this.dataSource.filter = this.valorFiltro.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  cargarSeleccionEstado() {
    if (this.estadosSeleccionados) {
      this.router.navigate(['.'], {
        relativeTo: this.route,
        queryParams: {
          estado: this.estadosSeleccionados,
        },
      });
    }
  }

  existenIncentivosSeleccionadas() {
    return this.invitaciones.some(
      (value) => value.remitenteSeleccionado || value.invitadoSeleccionado
    );
  }

  generarIncentivo() {
    this.qiipSpinnerService.show();
    const body = this.obtenerBody();
    this.dashboardService
      .generarIncentivos(body)
      .then((respuesta) => {
        this.validarRespuestaIncentivos(respuesta);
        this.consultarIncentivos();
      })
      .catch((reason) => {
        console.error('Error generando Incentivos', reason);
        this.qiipModalService.abrirModal(
          ModalType.ERROR,
          'Hay un error en el envo de incentivos'
        );
      })
      .finally(() => this.qiipSpinnerService.hide());
  }

  parsearTextoEstado(valor: string) {
    if (!valor) {
      return valor;
    }
    const palabra = valor
      .split('_')
      .map((s) => s.toLowerCase())
      .join(' ');
    return palabra[0].toUpperCase() + palabra.substr(1).toLowerCase();
  }

  calularTotalRemitente() {
    this.totalRemitente = this.invitaciones
      .filter((value) => value.remitenteSeleccionado)
      .reduce(
        (previousValue, currentValue) =>
          previousValue + currentValue.montoRemitente,
        0
      );
  }

  calularTotalInvitado() {
    this.totalInvitado = this.invitaciones
      .filter((value) => value.invitadoSeleccionado)
      .reduce(
        (previousValue, currentValue) =>
          previousValue + currentValue.montoRemitente,
        0
      );
  }

  private initColumnas() {
    this.columnasVisibles = [
      'nombreRemitente',
      'idUsarioRemitente',
      'contratoRemitente',
      'montoRemitente',
      'remitenteSeleccionado',
      'nombreInvitado',
      'idUsuarioInvitado',
      'contratoInvitado',
      'montoInvitado',
      'celularInvitado',
      'fechaInvitado',
      'estadoInvitacion',
      'invitadoSeleccionado',
    ];
  }

  private validarRespuestaIncentivos(respuesta: IncentivosGeneradosDTO) {
    if (respuesta.incentivos && respuesta.incentivos.length > 0) {
      const personaDTOS = respuesta.incentivos.map((value) => {
        const invitacionDTO = this.invitacionesTemp.find(
          (value1) => value.idInvitacion === value1.idInvitacion
        );
        if (invitacionDTO) {
          return invitacionDTO.remitente.idPersona === value.idPersona
            ? invitacionDTO.remitente
            : invitacionDTO.invitado;
        } else {
          return null;
        }
      });
      this.qiipModalService.abrirModal(
        ModalType.SUCCESS,
        ['Incentivos enviados correctamente'].concat(
          personaDTOS.map(
            (value) =>
              `Incentivo otorgado: ${value.valorIncentivo}, a ${value.nombre}`
          )
        )
      );
    } else {
      this.qiipModalService.abrirModal(
        ModalType.ERROR,
        'No se logro generar ningn incentivo'
      );
    }
  }

  private consultarIncentivos() {
    this.route.queryParams.subscribe((params) => {
      this.estadosQueryParams = params.estado;
      this.estadosSeleccionados = !this.estadosQueryParams
        ? []
        : this.estadosSeleccionados;
      this.consultarIncentivosPorEstados(this.estadosQueryParams);
    });
  }

  private consultarIncentivosPorEstados(estadosQueryParams: string[]) {
    this.reiniciarSeleccionTodos();
    this.dataSource = new MatTableDataSource([]);
    const periodo = this.form.value.periodo
      ? dayjs(this.form.value.periodo).format('YYYY-MM')
      : null;
    console.log('\n\n\n' + periodo);
    if (periodo) {
      this.qiipSpinnerService.show();
      this.dashboardService
        .consultarInvitaciones(periodo, estadosQueryParams)
        .then((respuesta) => {
          this.invitacionesTemp = respuesta.invitaciones;
          this.invitaciones = this.mappearInvitacion(respuesta.invitaciones);
          this.initDataSource(this.invitaciones);
          this.filtrar(this.valorFiltro);
        })
        .catch((reason) => {
          console.error('Error consultando invitaciones', reason);
          this.qiipModalService.abrirModal(
            ModalType.ERROR,
            'Ocurrio un error consultando la lista de invitaciones, intentalo ms tarde.'
          );
        })
        .finally(() => this.qiipSpinnerService.hide());
    }
  }

  private initDataSource(data: any[]) {
    this.dataSource = new MatTableDataSource(data);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.totalRemitente = 0;
    this.totalInvitado = 0;
  }

  private obtenerBody(): GenerarIncentivosDTO {
    let incentivos: GenerarIncentivoDTO[] = [];
    const invitacionesSeleccionadas = this.invitaciones.filter(
      (value) => value.remitenteSeleccionado || value.invitadoSeleccionado
    );
    invitacionesSeleccionadas.forEach((value) => {
      incentivos = value.remitenteSeleccionado
        ? incentivos.concat(
            this.obtenerIncentivo(
              value.idIncentivoRemitente,
              value.idInvitacion,
              value.idRemitente
            )
          )
        : incentivos;
      incentivos = value.invitadoSeleccionado
        ? incentivos.concat(
            this.obtenerIncentivo(
              value.idIncentivoInvitado,
              value.idInvitacion,
              value.idInvitado
            )
          )
        : incentivos;
    });
    return {
      correoOperador: this.correoOperador,
      incentivos,
    };
  }

  private obtenerIncentivo(
    idIncentivo,
    idInvitacion,
    idPersona
  ): GenerarIncentivoDTO {
    return {
      idIncentivo,
      idInvitacion,
      idPersona,
    };
  }

  private mappearInvitacion(invitaciones: InvitacionDTO[]): InvitacionData[] {
    return invitaciones.map((value) => ({
      idInvitacion: value.idInvitacion,
      estadoInvitacion: value.estadoInvitacion,
      nombreRemitente: value.remitente.nombre,
      idRemitente: value.remitente.idPersona,
      idUsuarioRemitente: value.remitente.idUsuario,
      contratoRemitente: value.remitente.contrato,
      montoRemitente: value.remitente.valorIncentivo,
      idIncentivoRemitente: value.remitente.idIncentivo,
      nombreInvitado: value.invitado.nombre,
      idInvitado: value.invitado.idPersona,
      idUsuarioInvitado: value.invitado.idUsuario,
      contratoInvitado: value.invitado.contrato,
      montoInvitado: value.invitado.valorIncentivo,
      idIncentivoInvitado: value.invitado.idIncentivo,
      celularInvitado: value.invitado.celular,
      fechaInvitado: value.fechaCreacion,
      remitenteSeleccionado: false,
      remitenteHabilitado:
        value.estadoInvitacion === EstadoInvitacion.APLICA_INCENTIVO,
      invitadoSeleccionado: false,
      invitadoHabilitado:
        value.estadoInvitacion === EstadoInvitacion.APLICA_INCENTIVO,
    }));
  }

  actualizarTodosRemitentes() {
    const datosRenderizados = this.obtenerDatosRenderizados();
    this.invitaciones.forEach((value) => {
      if (
        value.remitenteHabilitado &&
        datosRenderizados.some(
          (value1) => value1.idInvitacion === value.idInvitacion
        )
      ) {
        value.remitenteSeleccionado = this.seleccionarTodosRemitentes;
      }
    });
    this.calularTotalRemitente();
  }

  actualizarTodosInvitados() {
    const datosRenderizados = this.obtenerDatosRenderizados();
    this.invitaciones.forEach((value) => {
      if (
        value.invitadoHabilitado &&
        datosRenderizados.some(
          (value1) => value1.idInvitacion === value.idInvitacion
        )
      ) {
        value.invitadoSeleccionado = this.seleccionarTodosInvitados;
      }
    });
    this.calularTotalRemitente();
  }

  private obtenerDatosRenderizados() {
    const datosFiltrado = this.dataSource.sortData(
      this.dataSource.filteredData,
      this.dataSource.sort
    );
    return this.dataSource._pageData(datosFiltrado);
  }

  reiniciarSeleccionTodos() {
    this.seleccionarTodosInvitados = false;
    this.seleccionarTodosRemitentes = false;
  }

  consultarPorPeriodo() {
    const estados = this.route.snapshot.queryParams.estado;
    this.reiniciarSeleccionTodos();
    this.consultarIncentivosPorEstados(estados);
  }
}
