import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-modal-comentarios',
  templateUrl: './modal-comentarios.component.html',
  styleUrls: ['./modal-comentarios.component.scss'],
})
export class ModalComentariosComponent implements OnInit {
  comentario: string;
  _comentario: string;
  color: string;
  soloLectura: boolean = false;

  constructor(
    public dialogRef: MatDialogRef<ModalComentariosComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.comentario = data.comentario;
    this._comentario = data.comentario;
    this.color = data.color;
    this.soloLectura = data.soloLectura;
  }

  ngOnInit() {}

  enviar() {
    this.dialogRef.close(this.comentario === '' ? ' ' : this.comentario);
  }

  cancelar() {
    this.dialogRef.close(this._comentario);
  }
}
