import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { DashboardService } from '@app/modulos/dashboard/servicios/dashboard.service';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { flatMap, map, tap } from 'rxjs/operators';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { ITareaRevisionDocumento } from '@dashboard/modelo/interfaces/ITareaRevisionDocumento.type';
import { fromPromise } from 'rxjs/internal-compatibility';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { EstadoTarea } from '@dashboard/modelo/enums/EstadosTarea.enum';
import { ModalComentarioTareaGenericaComponent } from '@dashboard/componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { VerArchivoComponent } from '@dashboard/componentes/ver-archivo/ver-archivo.component';
import { IDocumentoUsuario } from '@dashboard/modelo/interfaces/IDocumentoUsuario.type';
import { throwError } from 'rxjs';

@Component({
  selector: 'app-tarea-documentos',
  templateUrl: './tarea-documentos.component.html',
  styleUrls: ['./tarea-documentos.component.scss'],
})
export class TareaDocumentosComponent implements OnInit {
  tareaDocumental: ITareaRevisionDocumento;
  idTarea: number;
  bloquearBoton = false;
  EstadoTarea = EstadoTarea;

  pageSizeOptions = [10, 25, 50, 100];
  columnas: string[] = [
    'id',
    'codigoTipoDocumento',
    'tipoArchivo',
    'fechaCreacion',
    'acciones',
  ];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public router: Router,
    public dialog: MatDialog,
    private location: Location,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private dashboarService: DashboardService,
    private customModalService: CustomModalService
  ) {}

  ngOnInit() {
    this.dataSource = new MatTableDataSource([]);
    this.consultarDatos();
  }

  consultarDatos() {
    this.route.params
      .pipe(
        map((value) => value.idTarea),
        tap((idTarea) => (this.idTarea = idTarea)),
        flatMap((idTarea) =>
          idTarea
            ? fromPromise(
                this.dashboarService.consultarTareaRevisionDocumentos(idTarea)
              )
            : throwError('Id no definido')
        )
      )
      .subscribe(
        (value) => {
          if (EstadoTarea[value.estadoTarea] === EstadoTarea.FINALIZADA) {
            this.router.navigate(['/dashboard']);
          }
          this.tareaDocumental = value;
          this.dataSource = new MatTableDataSource(
            this.tareaDocumental.documentos
          );
        },
        (error) => {
          console.error(
            'Error consultando tarea de revision de documentos:',
            error
          );
          this.mostarModalError();
          return this.router.navigate(['/dashboard']);
        }
      );
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }

  verDocumento(documento: IDocumentoUsuario) {
    this.dialog.open(VerArchivoComponent, {
      height: '70%',
      width: '50%',
      data: {
        idDocumento: documento.id,
      },
    });
  }

  descargarDocumento(documento: IDocumentoUsuario) {
    this.dashboarService
      .descargarDocumento(documento.id, 'archivo_' + documento.id)
      .subscribe((x) => {
        console.log('Documento descargado', x);
      });
  }

  abrirModalComentario() {
    const modal = this.abrirModal();
    modal.afterClosed().subscribe((comentario) => {
      return this.finalizarTarea(comentario);
    });
    return modal;
  }

  abrirModal() {
    return this.dialog.open(ModalComentarioTareaGenericaComponent, {
      data: {},
      panelClass: 'medium-screen-dialog',
    });
  }

  finalizarTarea(comentario: string): Promise<any> {
    if (comentario) {
      this.bloquearBoton = true;
      return this.dashboarService
        .finalizarTareaGenerica({
          idTarea: this.idTarea,
          comentario,
        })
        .then(() => {
          this.mostarModalSuccess();
          return this.router.navigate(['/dashboard']);
        })
        .catch((err) => {
          console.error('Error finalizando tarea: ', err);
          this.mostarModalError();
        })
        .finally(() => (this.bloquearBoton = false));
    }
  }
}
