import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
import { DashboardService } from '../../servicios/dashboard.service';
import { ITareaGenerica } from '../../modelo/interfaces/ITareaGenerica.type';
import { CustomModalType } from '../../modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '../../../../shared/servicios/custom-modal.service';
import { IEmpleadoRestringido } from '../../modelo/interfaces/IEmpleadoRestringido.type';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { INombreEmpresa } from '../../modelo/interfaces/INombreEmpresa.type';
import { EstadoTareaEmpleadoBloqueado } from '../../modelo/enums/EstadoTareaEmpleadoBloqueado.enum';
import { flatMap } from 'rxjs/operators';

@Component({
  selector: 'app-tarea-revision-listas-negras',
  templateUrl: './tarea-revision-listas-negras.component.html',
  styleUrls: ['./tarea-revision-listas-negras.component.scss'],
})
export class TareaRevisionListasNegrasComponent implements OnInit {
  detalleTarea: ITareaGenerica;
  datosEmpleado: IEmpleadoRestringido;
  form: FormGroup;
  cargando = false;
  nombreEmpresa$: Observable<INombreEmpresa>;
  EstadoTareaEmpleadoBloqueado = EstadoTareaEmpleadoBloqueado;

  constructor(
    public dashboarService: DashboardService,
    private location: Location,
    private fb: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private customModalService: CustomModalService
  ) {}

  ngOnInit() {
    this.form = this.fb.group({
      aceptaListasNegras: this.fb.control(null, Validators.required),
    });
    this.route.params
      .pipe(
        flatMap((params) => {
          const idTarea = +params.id_tarea;
          return this.dashboarService.consultarTareaPorId(idTarea);
        })
      )
      .subscribe(
        (tarea) => {
          this.detalleTarea = tarea;
          this.datosEmpleado = this.detalleTarea.datos as IEmpleadoRestringido;
          this.nombreEmpresa$ = this.dashboarService.consultarNombreEmpresaDeUsuario(
            this.datosEmpleado.idUsuario
          );
        },
        (error) => {
          this.router
            .navigate(['/dashboard'])
            .then(() => this.mostarModalError())
            .catch((reason) => console.error(reason));
          return console.error('ERROR consulta tarea generica', error);
        }
      );
  }

  guardar() {
    if (this.form.valid) {
      this.cargando = true;
      const estado: EstadoTareaEmpleadoBloqueado = this.form.getRawValue()
        .aceptaListasNegras;
      this.dashboarService
        .enviarComandoFinalizarTareaRevisionListasNegras({
          idTarea: this.detalleTarea.id,
          idUsuario: this.datosEmpleado.idUsuario,
          estado,
        })
        .then(() => {
          this.cargando = false;
          this.mostarModalSuccess();
          this.location.back();
        })
        .catch((err) => {
          this.cargando = false;
          this.mostarModalError();
        });
    }
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }
}
