import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { EstadoTarea } from '@app/modulos/dashboard/modelo/enums/EstadosTarea.enum';

@Component({
  selector: 'app-modal-comentario-tarea-generica',
  templateUrl: './modal-comentario-tarea-generica.component.html',
  styleUrls: ['./modal-comentario-tarea-generica.component.scss'],
})
export class ModalComentarioTareaGenericaComponent implements OnInit {
  comentario: string;
  EstadoTarea = EstadoTarea;

  constructor(
    public dialogRef: MatDialogRef<ModalComentarioTareaGenericaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { estado: EstadoTarea }
  ) {
    this.comentario = '';
  }

  ngOnInit() {}

  finalizar() {
    this.dialogRef.close(this.comentario);
  }

  cancelar() {
    this.dialogRef.close(null);
  }
}
