import { Component, OnInit, ViewChild } from '@angular/core';
import { DashboardService } from '../../servicios/dashboard.service';
import { ITarea } from '../../modelo/interfaces/ITarea.type';
import { DatePipe } from '@angular/common';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';

import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { ActivatedRoute, Router } from '@angular/router';
import { TipoTarea } from '@app/modulos/dashboard/modelo/enums/TipoTarea.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { ITareaGenerica } from '@app/modulos/dashboard/modelo/interfaces/ITareaGenerica.type';
import { ITipoTarea } from '@app/modulos/dashboard/modelo/interfaces/ITiposTareas.type';
import { ModalComentarioTareaGenericaComponent } from '@app/modulos/dashboard/componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { debounceTime, map } from 'rxjs/operators';
import { combineLatest } from 'rxjs';
import * as momentTimezone from 'moment-timezone';
import { environment } from '@env/environment';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { FormControl, Validators } from '@angular/forms';
import * as dayjs from 'dayjs';

@Component({
  selector: 'app-tareas-por-tipo',
  templateUrl: './tareas-por-tipo.component.html',
  styleUrls: ['./tareas-por-tipo.component.scss'],
})
export class TareasPorTipoComponent implements OnInit {
  pageSizeOptions = [10, 25, 50, 100];

  valorFiltro: string;
  seleccionarTodos: boolean;
  tiposTareas: ITipoTarea[];
  tipoTareaSeleccionada: ITipoTarea;
  paramTipoTarea: string;

  tareasPorTipo: any[] = [];
  tareas: ITareaGenerica[] = [];
  columnasBase: string[] = [];
  columnasTarea: string[] = [];

  columnasTipoTarea: string[] = [];
  columnasVisibles: string[] = [];
  dataSource: MatTableDataSource<any>;
  maxDate = new Date();
  periodo = new FormControl(new Date(), [Validators.required]);
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public dashboardService: DashboardService,
    private datePipe: DatePipe,
    public router: Router,
    private dialog: MatDialog,
    private customModalService: CustomModalService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.periodo.valueChanges.pipe(debounceTime(3000)).subscribe((value) => {
      const day = dayjs(value);
      if (day.isValid()) {
        this.actualizarListaPorTipo();
      }
    });
    this.initColumnas();
    this.consultarTiposTareas();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  verDetalleTarea(tarea: ITarea) {
    switch (TipoTarea[tarea.tipo]) {
      case TipoTarea.TAREA_DOCUMENTAL:
        this.router.navigate(['/dashboard/detalle-tarea', tarea.id]);
        break;
      case TipoTarea.REVISION_LISTAS_NEGRAS:
        this.router.navigate(['/dashboard/revision-listas-negras', tarea.id]);
        break;
      case TipoTarea.TAREA_DOCUMENTAL_GENERICA:
        this.router.navigate(['/dashboard/revision-documentos', tarea.id]);
        break;
      default:
        this.router.navigate(['/dashboard/detalle-generico', tarea.id]);
    }
  }

  cargarSeleccionTipoTarea() {
    if (this.tipoTareaSeleccionada) {
      this.router.navigate(['.'], {
        relativeTo: this.route,
        queryParams: {
          tipoTarea: this.tipoTareaSeleccionada.id,
        },
      });
    }
  }

  actualizarListaPorTipo() {
    this.valorFiltro = null;
    if (this.tipoTareaSeleccionada) {
      this.consultarTareas();
    }
  }

  cerrarTareasSeleccionadas(finalizar: boolean) {
    const tareas: ITareaGenerica[] = this.tareasPorTipo.filter(
      (value) => value.seleccionado
    );
    if (tareas.length > 0) {
      const modal = this.dialog.open(ModalComentarioTareaGenericaComponent, {
        data: {},
        panelClass: 'medium-screen-dialog',
      });
      modal.afterClosed().subscribe((comentario) => {
        if (comentario) {
          if (finalizar) {
            this.finalizarTareasGenericas(tareas, comentario);
          } else {
            this.reintentarTareasGenericas(tareas, comentario);
          }
        }
      });
      return modal;
    }
  }

  finalizarTareasGenericas(
    tareas: ITareaGenerica[],
    comentario: string
  ): Promise<any> {
    const promesas = tareas.map((value) => {
      this.dashboardService.finalizarTareaGenerica({
        idTarea: value.id,
        comentario,
      });
    });
    return Promise.all(promesas)
      .then(
        () => {
          this.mostarModalSuccess();
          this.consultarTareas();
        },
        (reason) => {
          this.mostarModalError();
          this.consultarTareas();
        }
      )
      .catch((err) => {
        this.mostarModalError();
        this.consultarTareas();
      });
  }

  reintentarTareasGenericas(
    tareas: ITareaGenerica[],
    comentario: string
  ): Promise<any> {
    const promesas = tareas.map((value) =>
      this.dashboardService.reintentarTareaGenerica({
        idTarea: value.id,
        comentario,
      })
    );
    return Promise.all(promesas)
      .then(() => {
        this.mostarModalSuccess();
        this.consultarTareas();
      })
      .catch((err) => {
        this.mostarModalError();
        this.consultarTareas();
      });
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }

  borrarFiltro() {
    this.valorFiltro = '';
    this.dataSource.filter = this.valorFiltro.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  esFecha(valor) {
    const date = new Date(valor);
    return !isNaN(valor)
      ? false
      : date instanceof Date && !isNaN(date.valueOf());
  }

  esObjeto(valor: any) {
    return typeof valor === 'object';
  }

  existenTareasSeleccionadas() {
    return this.tareasPorTipo.some((value) => value.seleccionado);
  }

  tareasSeleccionadasSonReintentables() {
    const tareas: ITareaGenerica[] = this.tareasPorTipo.filter(
      (value) => value.seleccionado
    );
    return tareas.length > 0 && tareas.every((value) => value.esReintentable);
  }

  actualizarCheckSeleccionTodos() {
    this.seleccionarTodos = this.dataSource.data.every((value) => {
      return value.seleccionado || value.deshabilitado;
    });
  }

  actualizarTareasSeleccionadas() {
    this.dataSource.data.forEach((value) => {
      if (!value.deshabilitado) {
        value.seleccionado = this.seleccionarTodos;
      }
    });
  }

  parsearStringMayus(valor: string) {
    let nombre = '';
    if (valor.includes('_')) {
      nombre = valor
        .split('_')
        .map((s) => s.toLowerCase())
        .join(' ')
        .replace(/^\w/, (c) => c.toUpperCase());
    } else {
      nombre = valor
        .split(/(?=[A-Z])/)
        .map((s) => s.toLowerCase())
        .join(' ')
        .replace(/^\w/, (c) => c.toUpperCase());
    }
    return nombre;
  }

  private initColumnas() {
    this.seleccionarTodos = false;
    this.columnasBase = ['id', 'fechaCreacion', 'criticidad', 'esReintentable'];
    this.columnasTarea = this.columnasBase.slice();
    this.columnasVisibles = [
      'select',
      'id',
      'fechaCreacion',
      'criticidad',
      'esReintentable',
      'acciones',
    ];
  }

  private consultarTareas() {
    this.initColumnas();
    const periodo = dayjs(this.periodo.value).format('YYYY-MM');
    this.dataSource = new MatTableDataSource([]);
    this.dashboardService
      .consultarTareasPorTipo(this.tipoTareaSeleccionada.id, periodo)
      .subscribe(
        (lista) => {
          this.tareas = lista.tareas.sort(this.ordenarPorFecha);
          this.extraerDatosPorTipoTarea();
        },
        (err) => this.customModalService.abrirModal(CustomModalType.ERROR)
      );
  }

  private consultarTiposTareas() {
    const _this = this;

    const param$ = this.route.queryParams.pipe(
      map((params) => {
        this.paramTipoTarea = params.tipoTarea;
        return this.paramTipoTarea;
      })
    );
    const consulta$ = this.dashboardService.consultarTiposTareas();
    combineLatest(param$, consulta$).subscribe(
      (value) => {
        if (value[1].tiposTareas) {
          this.tiposTareas = value[1].tiposTareas
            .map((tipo) => this.mappearNombresTipoTares(tipo, _this))
            .sort(this.ordenarPorNombre);
        }
        if (value[0]) {
          this.tipoTareaSeleccionada = this.tiposTareas.find((tipo) => {
            return tipo.id.toString() === this.paramTipoTarea;
          });
          this.actualizarListaPorTipo();
        }
      },
      () => this.customModalService.abrirModal(CustomModalType.ERROR)
    );
  }

  private ordenarPorNombre(a: ITipoTarea, b: ITipoTarea) {
    const bandA = a.nombre.toUpperCase();
    const bandB = b.nombre.toUpperCase();

    let comparison = 0;
    if (bandA > bandB) {
      comparison = 1;
    } else if (bandA < bandB) {
      comparison = -1;
    }
    return comparison;
  }

  private ordenarPorFecha(a: ITareaGenerica, b: ITareaGenerica) {
    let comparison = 0;
    if (a.fechaCreacion > b.fechaCreacion) {
      comparison = 1;
    } else if (a.fechaCreacion < b.fechaCreacion) {
      comparison = -1;
    }
    return comparison;
  }

  private mappearNombresTipoTares(tipoTarea: ITipoTarea, _this) {
    const nombre = _this.parsearStringMayus(tipoTarea.nombre);
    return {
      ...tipoTarea,
      nombre,
    };
  }

  private obtenerColumnasVisibles() {
    const select = ['select'];
    this.columnasTarea = this.columnasBase.concat(this.columnasTipoTarea);
    const columnas = select.concat(this.columnasTarea).concat('acciones');
    return columnas;
  }

  private extraerDatosPorTipoTarea() {
    let columnasTipoTarea = [];
    this.tareasPorTipo = this.tareas.slice().map((value) => {
      let datos: any = !value.datos
        ? {}
        : value.datos.json
        ? value.datos.json
        : value.datos;
      datos = this.eliminarPropiedadesBaseRepetidas(datos);
      datos = this.eliminarDatosOrigen(datos);
      datos = this.eliminarPropiedadesObjetosYNull(datos);
      columnasTipoTarea = this.obtenerPropiedadesJsonDatos(
        columnasTipoTarea,
        datos
      );
      return {
        ...value,
        ...datos,
        fechaCreacion: momentTimezone(value.fechaCreacion)
          .tz(environment.zone)
          .format('YYYY/MM/DD'),
        seleccionado: false,
        deshabilitado: this.bloquearSeleccionPorTipoTarea(value),
      };
    });
    this.initDataSource(this.tareasPorTipo, columnasTipoTarea);
  }

  private eliminarPropiedadesBaseRepetidas(datos: any) {
    this.columnasBase.forEach((columna) => {
      delete datos[columna];
    });
    return datos;
  }

  private eliminarDatosOrigen(datos: any) {
    delete datos.cola;
    delete datos.eventoOriginal;
    return datos;
  }

  private eliminarPropiedadesObjetosYNull(datos: any) {
    Object.keys(datos).forEach((columna) => {
      if (datos[columna] && this.esObjeto(datos[columna])) {
        delete datos[columna];
      }
      if (datos[columna] === null || datos[columna] === undefined) {
        delete datos[columna];
      }
    });
    return datos;
  }

  private obtenerPropiedadesJsonDatos(columnasTipo: string[], datos: any) {
    if (this.esObjeto(datos)) {
      const propiedades = Object.keys(datos);
      propiedades.forEach((propiedadJson) => {
        if (columnasTipo.indexOf(propiedadJson) === -1) {
          columnasTipo.push(propiedadJson);
        }
      });
      return columnasTipo;
    } else {
      return columnasTipo;
    }
  }

  private initDataSource(data: any[], columnas: string[]) {
    this.dataSource = new MatTableDataSource(data);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.columnasTipoTarea = columnas;
    this.columnasVisibles = this.obtenerColumnasVisibles();
  }

  private bloquearSeleccionPorTipoTarea(value: ITareaGenerica) {
    let bloquearSeleccion;
    switch (TipoTarea[value.tipo]) {
      case TipoTarea.TAREA_DOCUMENTAL:
        bloquearSeleccion = true;
        break;
      case TipoTarea.REVISION_LISTAS_NEGRAS:
        bloquearSeleccion = true;
        break;
      default:
        bloquearSeleccion = false;
    }
    return bloquearSeleccion;
  }
}
