import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { DashboardService } from '../../servicios/dashboard.service';
import { fromEvent } from 'rxjs';
import { ITarea } from '../../modelo/interfaces/ITarea.type';
import { EstadoTarea } from '../../modelo/enums/EstadosTarea.enum';
import { DatePipe } from '@angular/common';
import { HumanizePipe } from '@app/shared/pipes/HumanizePipe.pipe';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { ITareasPaginadas } from '@app/modulos/dashboard/modelo/interfaces/ITareasPaginadas.type';

import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { Router } from '@angular/router';
import { TipoTarea } from '@app/modulos/dashboard/modelo/enums/TipoTarea.enum';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';

@Component({
  selector: 'app-tareas',
  templateUrl: './tareas.component.html',
  styleUrls: ['./tareas.component.scss'],
})
export class TareasComponent implements OnInit {
  @ViewChild('filter', { static: true }) filter: ElementRef;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  tareas: ITarea[];
  tareasFiltradas: ITarea[];
  _length = 100;
  _pageSize = 10;
  _pageSizeOptions = [10, 25, 50, 100];
  _pageIndex = 0;
  _active = 'default';
  _direction = 'default';

  constructor(
    private dashboardService: DashboardService,
    private datePipe: DatePipe,
    public router: Router,
    private customModalService: CustomModalService,
    private humanizePipe: HumanizePipe
  ) {}

  ngOnInit() {
    this.consultarTareas();
    fromEvent(this.filter.nativeElement, 'keyup')
      .pipe(debounceTime(300), distinctUntilChanged())
      .subscribe(() => {
        if (this.filter.nativeElement.value) {
          const filterValue = this.filter.nativeElement.value
            .trim()
            .toLowerCase();
          this.tareasFiltradas = this.tareas.filter((tarea: ITarea) => {
            const { a, b, c, d, e } = this.extraerCriteriosBusqueda(tarea);
            return (a + b + c + d + e).includes(filterValue);
          });
        } else {
          this.tareasFiltradas = this.tareas;
        }
      });
  }

  private extraerCriteriosBusqueda(tarea: ITarea) {
    const a = this.datePipe.transform(
      tarea.fechaCreacion,
      'yyyy/MM/dd HH:mm:ss'
    );
    const b = this.humanizePipe.transform(tarea.tipo);
    const c = tarea.nombre.toLowerCase();
    const d = this.datePipe.transform(
      tarea.fechaCreacion,
      'd MMMM / y - h:mm a'
    );
    const e = tarea.criticidad;
    return { a, b, c, d, e };
  }

  changePage($event: PageEvent) {
    this._pageIndex = $event.pageIndex;
    this._pageSize = $event.pageSize;
    this.filter.nativeElement.value = '';
    this.consultarTareas();
  }

  sortData($event: Sort) {
    if ($event.direction) {
      this._active = $event.active;
      this._direction = $event.direction;
    } else {
      this._active = 'default';
      this._direction = 'default';
    }
    this.filter.nativeElement.value = '';
    this.consultarTareas();
  }

  verDetalleTarea(tarea: ITarea) {
    switch (TipoTarea[tarea.tipo]) {
      case TipoTarea.TAREA_DOCUMENTAL:
        this.router.navigate(['/dashboard/detalle-tarea', tarea.id]);
        break;
      case TipoTarea.REVISION_LISTAS_NEGRAS:
        this.router.navigate(['/dashboard/revision-listas-negras', tarea.id]);
        break;
      case TipoTarea.TAREA_DOCUMENTAL_GENERICA:
        this.router.navigate(['/dashboard/revision-documentos', tarea.id]);
        break;
      default:
        this.router.navigate(['/dashboard/detalle-generico', tarea.id]);
    }
  }

  private consultarTareas() {
    const offset = this._pageIndex * this._pageSize;
    this.dashboardService
      .consultarTareasPorEstado(
        EstadoTarea.PENDIENTE,
        offset,
        this._pageSize,
        this._active,
        this._direction
      )
      .then((tareasPaginadas: ITareasPaginadas) => {
        this.tareas = tareasPaginadas.tareas;
        this.tareasFiltradas = this.tareas;
        this._length = tareasPaginadas.cantidad;
      })
      .catch((err) =>
        this.customModalService.abrirModal(CustomModalType.ERROR)
      );
  }
}
