import {
  Component,
  OnInit,
  Inject,
  ElementRef,
  ViewChild,
} from '@angular/core';
import { DashboardService } from '@app/modulos/dashboard/servicios/dashboard.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-ver-archivo',
  templateUrl: './ver-archivo.component.html',
  styleUrls: ['./ver-archivo.component.scss'],
})
export class VerArchivoComponent implements OnInit {
  @ViewChild('iframe', { static: true }) iframe: ElementRef;
  srcDocumento$: Observable<any>;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<VerArchivoComponent>,
    private dashboardService: DashboardService
  ) {
    this.obtenerDocumento(data.idDocumento);
  }

  ngOnInit() {}

  obtenerDocumento(idDocumento: string) {
    if (idDocumento) {
      this.srcDocumento$ = this.dashboardService.previsualizarArchivo(
        idDocumento
      );
    }
  }

  onFrameLoad() {
    const document =
      this.iframe.nativeElement.contentDocument ||
      this.iframe.nativeElement.contentWindow;
    const head = document.head || document.getElementsByTagName('head')[0];
    if (head) {
      const style = document.createElement('style');
      style.type = 'text/css';
      if (style.styleSheet) {
        style.styleSheet.cssText = 'img { width:100% }';
      } else {
        style.appendChild(document.createTextNode('img { width:100% }'));
      }
      head.appendChild(style);
    }
  }
}
